import Vue from 'vue'
import VueRouter from 'vue-router'
import OverviewPage from '../views/pages/OverviewPage.vue'
import AmountPage from '../views/pages/AmountPage.vue'
import PaymentPage from '../views/pages/PaymentPage.vue'
import SettingsPage from '../views/pages/SettingsPage.vue'

//orders
import OrderHistoryPage from '../views/pages/Order/OrderHistoryPage.vue'
import OrderInfoPage from '../views/pages/Order/OrderInfoPage.vue'
import OrderSumPage from '../views/pages/Order/OrderSumPage.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/orderhistory',
    name: 'orderhistory',
    component: OrderHistoryPage
  },
  {
    path: '/orderinfo',
    name: 'orderinfo',
    component: OrderInfoPage
  },
  {
    path: '/ordersum',
    name: 'ordersum',
    component: OrderSumPage
  },
  {
    path: '/amount',
    name: 'amount',
    component: AmountPage
  },
  {
    path: '/',
    name: 'overview',
    component: OverviewPage
  },
  {
    path: '/payment',
    name: 'payment',
    component: PaymentPage
  },
  {
    path: '/settings',
    name: 'settings',
    component: SettingsPage
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
