<template>
  <div class="dropdown dropdown_currency">
        <div class="dropdown__current" @click="showOptions = !showOptions">
            <div class="dropdown__icon">
                <img src="@/assets/g16.png" alt="Bitcoin">
            </div>
            <div class="dropdown__title">{{ currentCurrency.title }}</div>
            <div class="dropdown__arrow">
                <svg xmlns="http://www.w3.org/2000/svg" width="11" height="6" viewBox="0 0 11 6" fill="none">
                    <path fill-rule="evenodd" clip-rule="evenodd" d="M5.5 6C5.38271 6 5.26312 5.95371 5.17343 5.86344L0.134539 0.789854C-0.0448463 0.609316 -0.0448463 0.317678 0.134539 0.13714C0.313924 -0.0457132 0.603701 -0.0457132 0.783086 0.13714L5.5 4.88205L10.2146 0.13714C10.394 -0.0457132 10.6861 -0.0457132 10.8655 0.13714C11.0448 0.317678 11.0448 0.609316 10.8655 0.789854L5.82427 5.86344C5.73458 5.95371 5.61729 6 5.5 6Z" fill="black"/>
                </svg>
            </div>
        </div>
        <transition name="dropdown-options">
            <div class="dropdown__options" v-show="showOptions">
                <div
                    class="dropdown__option"
                    v-for="(item, i) in currencyOptions"
                    :key="i"
                    @click="changeCurrentCurrency(i)"
                >
                    <div class="dropdown__icon" >
                        <img src="@/assets/g16.png" alt="Bitcoin">
                    </div>
                    <div class="dropdown__title">{{ item.title }}</div>
                </div>
            </div>
        </transition>
  </div>
</template>

<script>
export default {
    data() {
        return {
            showOptions: false,
            currencyOptions: [
                {
                    icon: "@/assets/g16.png",
                    title: "Bitcoin",
                    active: true
                },
                {
                    icon: "@/assets/g16.png",
                    title: "Bitcoin1",
                    active: false
                },
                {
                    icon: "@/assets/g16.png",
                    title: "Bitcoin2",
                    active: false
                }
            ]
        }
    },
    computed: {
        currentCurrency() {
            return this.currencyOptions.find( item => {
                return item.active === true;
            })
        }
    },
    methods: {
        changeCurrentCurrency(index) {
            this.currencyOptions.map( (item, i) => {
                if(item.active)
                    this.currencyOptions[i].active = false;
            });
            this.showOptions = false;
            this.currencyOptions[index].active = true;
        }
    }
}
</script>

<style lang="scss">
.dropdown__options {
    transition: all .3s ease-in-out; 
}
.dropdown-options-enter-active {
  opacity: 1;
}
.dropdown-options-leave-active {
  opacity: 0;
}
</style>