<template>
    <div class="range-slider-wrap">
        <div class="range-slider-title"> {{title}} </div>
        <div class="range-slider-unit">
            {{ unit }}
        </div>
        <range-slider
            class="slider"
            :min="min"
            :max="max"
            step="1"
            v-model="sliderV"
        >
        </range-slider>

        <div v-if="desc" class="range-slider-desc">{{desc}}</div>
    </div>
</template>

<script>
import RangeSlider from 'vue-range-slider'
import 'vue-range-slider/dist/vue-range-slider.css'

export default {
  data () {
    return {
    }
  },
  computed: {
      sliderV() {
          return this.sliderValue;
      }
  },
  props: {
    title: {
        type: String
    },
    unit: {
        type: String
    },
    min: {
        type: Number
    },
    max: {
        type: Number
    },
    sliderValue: {
        type: Number
    },
    desc: {
        type: String
    }
  },
  components: {
    RangeSlider
  }
}
</script>

<style lang="scss">
.range-slider-wrap {
    width: 220px;
}
.range-slider {
    padding: 0;
    width: 100%;
}
.range-slider-knob {
    width: 6px;
    height: 6px;
    background: #181818;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    border: none;
}
.range-slider-rail, .range-slider-fill {
    height: 2px;
    border-radius: 4px;
    background: #E8E8E8;
}
.range-slider-unit {
    font-family: 'Muli';
    font-style: normal;
    font-weight: bold;
    font-size: 10px;
    line-height: 7px;
    color: #555555;
    text-align: right;
}
.range-slider-title {
    font-family: 'Muli';
    font-style: normal;
    font-weight: 900;
    font-size: 10px;
    line-height: 13px;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    color: #44444F;
    margin-bottom: 30px;
}
.range-slider-desc {
    font-family: 'Muli';
    font-style: normal;
    font-weight: normal;
    font-size: 12px;
    line-height: 15px;
    color: #000000;
    margin-top: 3px;
}
</style>